body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.progressBarContainer {
  height: 6px;
  width: 100%;
  box-sizing: border-box; }
  .progressBarContainer .progressBar {
    height: 100%;
    background: #fce037;
    /* Old browsers */
    /* FF3.6-15 */
    /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to right, #fce037 0%, #26bf42 100%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='$ice_yellow', endColorstr='$ice_green', GradientType=1); }

[data-complete=true] .progressBar {
  background: #26bf42; }

.completed {
  background: #26bf42; }

table {
  margin: auto; }
  table th {
    padding: 5px; }
    table th span {
      margin-left: 5px; }
  table tr:nth-child(2n + 2) {
    background: #eee; }
  table td {
    padding: 5px; }

body {
  background: #fff;
  color: #666;
  font-family: 'Oxygen', Arial, sans-serif;
  margin: 0; }

h2 {
  color: #0C78C5;
  text-align: left;
  margin-top: 0;
  margin-bottom: 8px; }

h4 {
  margin: 0 0 10px;
  text-align: center; }

#ice-age {
  padding: 60px 12px 0; }

.hide {
  display: none; }

#ice-age-overlay {
  padding: 20px;
  width: 80%;
  box-sizing: border-box;
  position: fixed;
  left: 0;
  right: 0;
  margin: auto;
  color: black;
  font-size: 16px;
  z-index: 50;
  background-color: white; }

#overlay-screen {
  background-color: #000;
  opacity: 0.8;
  width: 100%;
  position: fixed;
  z-index: 30; }

.overlay-heading {
  text-align: left;
  text-transform: capitalize; }

#info-view a {
  display: block;
  margin-bottom: 5px; }

.gallery {
  margin-left: 20px;
  font-weight: normal; }

.fa-check-circle {
  color: #26bf42;
  margin-right: 2px; }

header nav {
  position: fixed;
  display: -webkit-flex;
  display: flex;
  width: 100%;
  background: black;
  color: white;
  font-weight: bold;
  text-align: center;
  cursor: pointer;
  height: 40px;
  z-index: 10;
  overflow: hidden;
  box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.4); }
  header nav .selected {
    background: #0C78C5; }
  header nav .nav-item {
    width: 50%;
    padding: 10px;
    box-sizing: border-box;
    color: white;
    text-decoration: none; }

#microphone {
  width: 10%;
  font-size: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  border-left: 2px solid white; }
  #microphone.active i {
    color: #0C78C5; }

#segments-view {
  /*
Styles for list of segments
*/ }
  #segments-view .segment-container {
    width: 100%;
    display: inline-block;
    margin-bottom: 55px; }
  #segments-view .county-name a {
    text-decoration: none !important;
    color: #0C78C5 !important; }
  #segments-view #segment-filter-container {
    font-size: 20px;
    font-weight: bold;
    font-family: 'Oxygen', sans-serif;
    width: 30%;
    background: #eee;
    float: left;
    box-sizing: border-box; }
    #segments-view #segment-filter-container select {
      display: none;
      border: none;
      font-size: 19px;
      font-weight: bold;
      padding: 5px 0;
      font-family: 'Oxygen', sans-serif;
      color: #666; }
  #segments-view .terminus-container {
    margin: 10px 0; }
  #segments-view #segment-filter {
    background: #eee;
    box-sizing: border-box; }
    #segments-view #segment-filter .selected {
      background: white !important; }
    #segments-view #segment-filter .complete {
      background: #fce037; }
    #segments-view #segment-filter ul {
      list-style: none;
      padding: 0; }
      #segments-view #segment-filter ul > li > a {
        font-size: 14px;
        display: block;
        padding: 8px;
        color: black;
        text-decoration: none; }
  #segments-view #segment-list {
    float: left;
    width: 70%;
    padding: 0 10px;
    box-sizing: border-box; }
  #segments-view .segment-name {
    text-align: left;
    font-weight: bold; }
  #segments-view .segment-summary {
    margin-bottom: 20px; }
  #segments-view .segment {
    width: 100%;
    box-sizing: border-box;
    min-height: 150px; }
  #segments-view .segment-details {
    position: relative;
    text-transform: capitalize; }
    #segments-view .segment-details span {
      position: relative;
      margin-right: 5px; }
  #segments-view .yes {
    cursor: pointer; }
    #segments-view .yes i {
      color: #26bf42; }
  #segments-view .no-hiking {
    margin: 15px 0 5px;
    font-weight: bold;
    color: #C50401; }
  #segments-view .segment-info {
    width: 50%;
    display: inline-block; }
  #segments-view .extra-info {
    margin-top: 20px;
    font-size: 25px;
    display: -webkit-flex;
    display: flex; }
    #segments-view .extra-info i {
      margin-right: 10px; }
    #segments-view .extra-info .yes .fa-tint {
      color: #0C78C5; }
  #segments-view #segment-list a {
    color: #236B8E; }
  #segments-view a.location {
    color: #236B8E;
    display: block;
    clear: both; }
  #segments-view .easy {
    color: #017927; }
  #segments-view .difficult {
    color: #C50401; }
  #segments-view .user-segments-view {
    margin-bottom: 15px; }
  #segments-view .county h3 {
    margin: 0 0 5px; }
  #segments-view .secondary-location {
    margin-top: 10px; }
  #segments-view .location-based-info {
    margin: 16px 0; }
  #segments-view #ice-age-overlay {
    padding: 20px;
    width: 80%;
    box-sizing: border-box;
    position: fixed;
    left: 0;
    right: 0;
    margin: auto;
    color: black;
    font-size: 16px;
    z-index: 50;
    background-color: white; }
  #segments-view #overlay-screen {
    background-color: #000;
    opacity: 0.8;
    width: 100%;
    position: fixed;
    z-index: 30; }
  #segments-view .overlay-heading {
    text-align: left;
    text-transform: capitalize; }
  @media (max-width: 600px) {
    #segments-view #segment-filter-container {
      background: white;
      width: 100%; }
      #segments-view #segment-filter-container select {
        display: block;
        margin-bottom: 10px;
        width: 100%; }
    #segments-view #segment-filter {
      width: 100%;
      display: none; }
    #segments-view #segment-list {
      width: 100%; }
    #segments-view .segment-info {
