#progress-view .user-container {
  display: block;
  width: 100%;
  vertical-align: top;
  padding: 0 5px 35px;
  box-sizing: border-box; }

#progress-view .county {
  margin-bottom: 25px; }
  #progress-view .county h3 {
    margin: 0;
    cursor: pointer;
    font-size: 1em; }
  #progress-view .county .segment {
    padding: 10px 5px; }
  #progress-view .county .segment-name {
    font-size: 0.9em;
    font-weight: bold; }
    #progress-view .county .segment-name.complete i {
      color: #26bf42;
