    cursor: pointer; }

#progress-view .user-segment-container {
  margin-bottom: 15px; }

#progress-view #progress-view h4 {
  text-align: left; }

#progress-view .container {
  margin: 0 0 35px; }

#progress-view .user-header {
  font-weight: bold;
